import * as React from 'react';

import Form, {
  CheckboxFieldProps,
  DescriptionField,
  FormProps,
  IFormCallbackProps,
  IFormUpdateProps,
  NameField,
  SelectFieldProps,
  TextChoicesType,
  TextFieldProps,
  useForm,
} from '@miq/formjs';
import { getCN, isRequired as isR } from '@miq/utiljs';

import { emitter } from '../events';
import { CategoryChoicesType } from '../types';

import { StaffProduct, TStaffProductInstance } from './models';
import { URLSearchParamsInit, useSearchParams } from 'react-router-dom';
import { Button } from '@miq/componentjs';
import { SupplierOrder } from '../Supplier';

/**
 *
 */

interface IProductUpdateFormProps extends IFormUpdateProps<TStaffProductInstance> {
  onWillSubmit?: () => void;
  fields?: string[];
}

export const ProductQuickUpdateForm = (
  props: Omit<IProductUpdateFormProps, 'context'> & { categories: CategoryChoicesType; orderSlug?: string }
) => {
  const { instance, categories, orderSlug, ...rest } = props;

  const form = useForm({
    name: instance.name || '',
    category: instance.category || '',
    description: instance.description || '',
    retail_price: instance?.retail_price || 0.0,
    is_pre_sale: instance.is_pre_sale || false,
    is_pre_sale_text: instance.is_pre_sale_text || '',
  });

  const { errors } = form;
  const { fields = ['name', 'category', 'description', 'retail_price', 'is_pre_sale', 'is_pre_sale_text'] } = props;

  return (
    <ProductUpdateForm {...rest} context={form} instance={instance} fields={fields}>
      <ProductForm.NameField error={errors.name} />

      <ProductForm.DescriptionField error={errors.description} />

      <div className="d-grid grid-md-3 gap-2">
        {categories && <ProductForm.CategoryField categories={categories} error={errors.category} />}
        <ProductForm.RetailPriceField error={errors.retail_price} />
      </div>

      <ProductForm.PresaleCheckboxField label="En pré-vente" error={errors.is_pre_sale} />
      {form.values.is_pre_sale && (
        <ProductForm.PresaleTextField label="Pre-sale text" placeholder="Explain ..." error={errors.is_pre_sale_text} />
      )}

      <div className="d-flex align-items-center justify-content-between">
        <div>
          {orderSlug && instance?.slug && (
            <Button
              onClick={() => {
                SupplierOrder({ slug: orderSlug }).destroyItem(instance.slug!);
              }}
            >
              Remove from order
            </Button>
          )}
        </div>

        <Form.Submit value="Sauvegarder" className="btn-primary-3 btn-md" />
      </div>
    </ProductUpdateForm>
  );
};

export function ProductUpdateForm({ children, ...props }: IProductUpdateFormProps) {
  const {
    instance = isR('StaffProduct instance'),
    fields = isR('fields instance'),
    onSuccess,
    onError,
    ...rest
  } = props;

  const { context } = rest;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    props?.onWillSubmit?.();

    const fd: any = {};
    fields.forEach((field) => {
      const value = context.values[field];
      if (value == null) return;
      if (field == 'is_pre_sale_dt' && !value) return;

      fd[field] = value;
    });

    return instance
      .update(fd)
      .then(({ data }: any) => {
        onSuccess?.(data);
        emitter.update(data);
      })
      .catch((err: any) => {
        if (onError) return onError(err);
        return context.handleError(err);
      });
  };

  return (
    <ProductForm {...rest} onSubmit={handleSubmit}>
      {children}
    </ProductForm>
  );
}

/**
 *
 */

export interface ProductCreateFormProps extends IFormCallbackProps {}

export const ProductCreateForm: React.FC<ProductCreateFormProps> = (props) => {
  const form = useForm({ name: '', retail_price: '', description: '' });

  const { errors } = form;

  const handleCreate = (e: React.FormEvent) => {
    e.preventDefault();
    StaffProduct()
      .create(form.values)
      .then((res: any) => props?.onSuccess?.(res))
      .catch((err: any) => form.handleError(err));
  };

  return (
    <ProductForm context={form} onSubmit={handleCreate}>
      <ProductForm.NameField label="Nom du produit" error={errors.name} />
      <ProductForm.RetailPriceField required={false} label="Prix du produit" error={errors.retail_price} />
      <ProductForm.DescriptionField label="Description du produit" error={errors.description} />

      <Form.Submit value="Sauvegarder" className="btn-primary-3" />
    </ProductForm>
  );
};

/**
 *
 */

export const ProductForm = (props: FormProps) => <Form {...props} />;

/**
 *
 */

ProductForm.NameField = (props: TextFieldProps) => (
  <NameField placeholder="Entrez un titre clair et concis ..." {...props} />
);
ProductForm.DescriptionField = (props: TextFieldProps) => (
  <DescriptionField placeholder="Décrivez les caractéristiques et les avantages ..." {...props} />
);

ProductForm.CategoryField = (props: SelectFieldProps & { categories: CategoryChoicesType }) => {
  const { label, text, error, categories, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Select {...rest} required name="category" nullValue={{ label: 'Sélectionnez une catégorie ...' }}>
        {categories?.items?.map((cat) => {
          return <Form.Option {...cat} key={cat.value} />;
        })}
      </Form.Select>
    </Form.Field>
  );
};

ProductForm.SalePriceField = (props: TextFieldProps) => {
  const { label, text, error, ...rest } = props;
  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text
        aria-describedby="sale_price_label"
        min="0"
        step="0.01"
        required
        placeholder="Prix réduit"
        {...rest}
        type="number"
        name="sale_price"
      />
    </Form.Field>
  );
};

ProductForm.OnSaleCheckboxField = (props: CheckboxFieldProps) => {
  const { label, text, error, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }} checkbox>
      <Form.Checkbox {...rest} name="is_on_sale" />
    </Form.Field>
  );
};

ProductForm.PresaleCheckboxField = (props: CheckboxFieldProps) => {
  const { label, text, error, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }} checkbox>
      <Form.Checkbox {...rest} name="is_pre_sale" />
    </Form.Field>
  );
};

ProductForm.PresaleTextField = (props: TextFieldProps) => {
  const { label, text, error, ...rest } = props;
  return (
    <Form.Field {...{ label, text, error }}>
      <Form.TextAreaX placeholder="" {...rest} name="is_pre_sale_text" aria-describedby="is_pre_sale_text_label" />
    </Form.Field>
  );
};

ProductForm.RetailPriceField = (props: TextFieldProps) => {
  const { label, text, error, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text
        required
        placeholder="Prix"
        aria-describedby="retail_price_label"
        min="0"
        step="0.01"
        {...rest}
        type="number"
        name="retail_price"
      />
    </Form.Field>
  );
};

ProductForm.StageSelectField = (props: SelectFieldProps & { stages: TextChoicesType }) => {
  const { label, text, error, stages, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Select required name={'stage'} nullValue={{ label: 'Select stage' }} {...rest}>
        {stages?.map?.((stage) => {
          return <Form.Option {...stage} key={stage.value} />;
        })}
      </Form.Select>
    </Form.Field>
  );
};

type ProductListFilterFormProps = {
  children?: React.ReactNode;
  categories?: CategoryChoicesType;
};

export const ProductListFilterForm = ({ children, ...props }: ProductListFilterFormProps) => {
  const [params, setParams] = useSearchParams();
  const [q, setQ] = React.useState<string>(params.get('q') || '');
  const { categories, ...rest } = props;

  return (
    <form
      {...rest}
      action="."
      method="GET"
      onSubmit={(e) => {
        e.preventDefault();

        if (!q) params.delete('q');
        else params.set('q', q);
        params.delete('page');

        setParams(params);
      }}
    >
      {children || (
        <>
          <div className="mb-1 d-flex ">
            <input
              type="text"
              name="q"
              value={q}
              onChange={(e) => {
                const { value } = e.target;
                setQ(value);
                if (!value && params.get('q')) params.delete('q');
              }}
              className="miq-form-input flex-1"
              placeholder="Cherchez un produit par nom, categorie ou numero de serie ..."
            />
            <Form.Submit value={'Search'} className="btn-primary-3 ms-1" />
          </div>

          {categories && <ProductListFilterForm.Category {...{ params, setParams, categories }} />}

          <ProductListFilterForm.Published {...{ params, setParams }} />
          <ProductListFilterForm.Sale {...{ params, setParams }} />
          <ProductListFilterForm.PreSale {...{ params, setParams }} />
        </>
      )}
    </form>
  );
};

type TParamsProps = {
  params: URLSearchParams;
  setParams: (
    nextInit: URLSearchParamsInit,
    navigateOptions?:
      | {
          replace?: boolean | undefined;
          state?: any;
        }
      | undefined
  ) => void;
};

type QuerySelectFieldProps = TParamsProps & {
  emptyLabel?: string;
  name: string;
  children?: React.ReactNode;
};

ProductListFilterForm.Category = (props: Omit<QuerySelectFieldProps, 'name'> & { categories: CategoryChoicesType }) => {
  const { categories, ...rest } = props;

  return (
    <QuerySelectField emptyLabel="Categorie ..." {...rest} name="cat">
      {categories.items.map((cat) => {
        return (
          <option value={cat.value} key={cat.slug}>
            {cat.label}
          </option>
        );
      })}
    </QuerySelectField>
  );
};

ProductListFilterForm.Published = (props: Omit<QuerySelectFieldProps, 'name'>) => {
  return (
    <QuerySelectField emptyLabel="Tous les produits ..." {...props} name="published">
      <option value="include">Produits publiés</option>
      <option value="exclude">Produits non publiés</option>
    </QuerySelectField>
  );
};

const QuerySelectField = ({ children, ...props }: QuerySelectFieldProps) => {
  const { name, params, setParams, emptyLabel } = props;
  return (
    <select
      className="miq-select"
      name={name}
      value={params.get(name) || ''}
      onChange={(e) => {
        const { value } = e.target;

        if (!value) params.delete(name);
        else params.set(name, value);
        params.delete('page');

        setParams(params);
      }}
    >
      {emptyLabel && <option value="">{emptyLabel}</option>}
      {children}
    </select>
  );
};

ProductListFilterForm.PreSale = (props: Omit<QueryToggleFieldProps, 'name' | 'label'>) => {
  return <QueryToggleField {...props} name="presale" label="En pré-vente" />;
};

ProductListFilterForm.Sale = (props: Omit<QueryToggleFieldProps, 'name' | 'label'>) => {
  return <QueryToggleField {...props} name="sale" label="En promo" />;
};

type QueryToggleFieldProps = Omit<QuerySelectFieldProps, 'emptyLabel'> & { label: string; inputId?: string };

const QueryToggleField = (props: QueryToggleFieldProps) => {
  const { label, name, params, setParams, inputId } = props;

  return (
    <div className={getCN(['miq-query-filter-toggle d-flex align-items-center'])}>
      <input
        type="checkbox"
        className="miq-checkbox me-1"
        id={inputId}
        onChange={(e) => {
          const { checked } = e.target;
          if (!checked) params.delete(name);
          else params.set(name, `${checked}`);
          params.delete('page');
          setParams(params);
        }}
        checked={Boolean(params.get(name)) || false}
      />
      <label htmlFor={inputId}>{label}</label>
    </div>
  );
};
