import * as React from 'react';

import { useRequest } from '@miq/hookjs';
import Staff from '@miq/staffjs';
import { getCN, Response } from '@miq/utiljs';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { SupplierOrder } from './models';
import { Pagination, Table } from '@miq/componentjs';
import { SupplierOrderType } from '../types';
import { SupplierOrderDeleteButton } from './components';

export default function SupplierOrderIndexView() {
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const page = params.get('page');

  const { res, removeItem } = useRequest(() => SupplierOrder().list({ params: { page } }), { refreshDeps: [page] });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View
      title="Orders"
      back="/staff/store/"
      actions={
        <Staff.Button
          onClick={() => {
            SupplierOrder()
              .create({})
              .then(({ data }: any) => {
                return navigate(`${data.slug}/`);
              })
              .catch((err: any) => {
                // console.log(err);
              });
          }}
          className="btn-primary-3"
          requiredPerms={['store.add_supplierorder']}
          title={'Add a new order'}
        >
          Add order
        </Staff.Button>
      }
      requiredPerms={['store.view_supplierorder']}
    >
      <Staff.Section footer={<Pagination {...r.data} component={Staff.Link} to />}>
        <Table
          border
          items={r.items}
          header={
            <>
              <Table.Th />
              <Table.Th>Items</Table.Th>

              <Table.Th>Total Cost</Table.Th>
              <Table.Th>Est. Revenue</Table.Th>
              <Table.Th />
            </>
          }
          renderItem={(item: SupplierOrderType) => {
            const { name = 'New order', slug, is_paid, items, items_revenue } = item;

            return (
              <Table.Tr key={slug}>
                <Table.Td>
                  <Staff.Link to={`${slug}/`} className="">
                    <div className={getCN(['fw-bold text-ellipsis', is_paid && 'text-line-through'])}>{name}</div>
                  </Staff.Link>
                  <span className="text-sm">{item.avg_cost}</span>
                </Table.Td>

                <Table.Td>{items?.length}</Table.Td>
                <Table.Td>
                  <Price {...item.total_cost_data} />
                </Table.Td>
                <Table.Td>
                  <Price {...items_revenue} />
                </Table.Td>

                <Table.Td>
                  <SupplierOrderDeleteButton instance={SupplierOrder(item)} onSuccess={() => removeItem?.({ slug })} />
                </Table.Td>
              </Table.Tr>
            );
          }}
          className="w-100"
        />
      </Staff.Section>
    </Staff.View>
  );
}

const Price = (props: any) => {
  const { amountWithSymbol } = props;
  return <div className="miq-price">{amountWithSymbol}</div>;
};

export * from './models';

